import {LOGIN} from "../actionTypes/authType";
import {API_PATH, TOKEN_NAME} from "../../tools/constants";
import axios from "axios";
import {toast} from "react-toastify";


export function updateState (data){
    return {
        type: LOGIN,
        payload: data
    }
}


export function LoginUser(data, history){


    return function (dispatch){


        axios.post(API_PATH + "auth/signIn" , data)
            .then(async res => {
                if (res.data.accessToken) {
                    await localStorage.setItem(TOKEN_NAME, res.data.tokenType + " " + res.data.accessToken);
                    dispatch(updateState({loginModal: false}))
                    history.push("/edit");
                } else {
                    toast.error("Xatolik");
                }
            })
            .catch(err =>{
                if (err.response){
                    if(err.response.data.status === 401){
                        toast.error("Bunday foydalanuvchi mavjud emas")
                    } else {
                        toast.error("Xatolik");
                    }
                }
            })


    }
}



export function regstration(data){
    return function (dispatch   ){
        dispatch(updateState({ firstDataForRegstration: data}))
        dispatch(updateState({regstrModalNext: true}))
        dispatch(updateState({regstrModal: false}))



    }
}


export function regstrationSecond(data){
    return function (dispatch, getState  ){
        dispatch(updateState( {  firstDataForRegstration: data}))
        dispatch(updateState({regstrModalNext: true}))
        dispatch(updateState({regstrModal: false}))



    }
}






export function getRegion(){
    return function (dispatch){
        axios.get(API_PATH + "region")
            .then(res =>{
                dispatch(updateState({region: res.data}))
            })

    }
}



