import React, {useState} from 'react';
import {Modal} from "reactstrap";
import {AvField, AvForm} from "availity-reactstrap-validation";
import {connect} from "react-redux";
import {LoginUser, updateState} from "../../redux/action/authAction";

const ForLogin = (props) => {
    const [pass3, setPass3] = useState(false)
    const passThreeChange = () => {
        setPass3(!pass3)

    }


    const openModal3 =()=>{

        props.updateState({loginModal: !props.loginModal})


    }
    const openModal4 =()=>{

        props.updateState({loginModalForgetPass: !props.loginModalForgetPass})
        props.updateState({loginModal: false})



    }


    const loginFunction =(event, value)=>{
        props.LoginUser(value, props.history)

    }
    return (
        <div>
            <Modal isOpen={props.loginModal} className="login-modal" toggle={openModal3}>



                <div className="reg-form-login">

                    <div className="close-btn">

                        <button onClick={  openModal3}>
                            <img src="/img/icon/close.png"  />
                        </button>

                    </div>
                    <h2>Tizimga kirish</h2>


                    <div  >
                        <AvForm className="form-group-val" onValidSubmit={loginFunction}>

                            <AvField name="login"    placeholder="Login  " type="text"/>

                            <div className="half-region">
                                <div className="row">


                                    <div className="col-md-12">
                                        <AvField name="password"  placeholder="Parol kiriting *" type={pass3 ? "text" : "password"}/>

                                        <button type="button" onClick={passThreeChange}>

                                            {
                                                pass3 ?  <img src="/img/icon/Pass.png" alt=""/>
                                                    :
                                                    <img src="/img/icon/showPass.png" alt=""/>
                                            }
                                        </button>

                                    </div>

                                </div>
                            </div>

                            <button className="next-btn"  >Tizimga kirish</button>
                        </AvForm>


                        <h6 className="forget-password">Login yoki parolni <button onClick={openModal4}> unutdingizmi?</button></h6>
                    </div>
                </div>

            </Modal>



            <Modal isOpen={props.loginModalForgetPass}>

                <div className="reg-form-login">

                    <div className="close-btn">

                        <button onClick={  openModal4}>
                            <img src="/img/icon/close.png"  />
                        </button>

                    </div>
                    <h2>Parolni qayta tiklash</h2>


                    <div  >
                        <AvForm className="form-group-val">

                            <AvField name="teat"    placeholder="Telifon raqamani kiriting  " type="text"/>



                            <button className="next-btn"  >Kodini olish</button>
                        </AvForm>


                    </div>
                </div>
            </Modal>
        </div>
    );
};



const mapStateToProps = (state) =>{
    return {
        regstrModal: state.login.regstrModal,
        regstrModalNext: state.login.regstrModalNext,
        loginModal: state.login.loginModal,
        loginModalForgetPass: state.login.loginModalForgetPass,

    }
}

export default connect(mapStateToProps, {updateState, LoginUser})(ForLogin)










