import {LOGIN, UPDATE_STATE} from "../actionTypes/authType";

const initialState = {
    isLoading: false,
    regstrModal: false,
    regstrModalNext: false,
    loginModal: false,
    loginModalForgetPass: false,
    region: [],
    firstDataForRegstration: {}

}

export const loginReducer = (state = initialState, action) => {

    switch (action.type){
        case LOGIN:
            return {...state,  ...action.payload}

            break;

        default: return state;
    }
}